import React, {Component} from "react";

import ComparatorRowComponent from "./ComparatorRowComponent";


export default class ComparatorSectionComponent extends Component {

    constructor(props) {
        super(props);

    }

    componentDidMount() {
        if (this.componentNeedInitialization("comparator-expander")) {
            OPL.UI.initModules(document.getElementById("comparator-expander-parent"));
        }
    }

    componentNeedInitialization(name) {
        return document.getElementById(name) != null
            && !document.getElementById(name).hasAttribute("data-js-processed")
    }

    render() {
        return (
            (this.props.section.attributesRowDTOList && this.props.section.attributesRowDTOList.length > 0) ?
                <div key={this.props.section.name + this.props.idx}
                     className="opl-section opl-section--expander is-expanded u-no-padding">
                    <div className="opl-section__header u-margin-m u-margin-top-m">
                        <h3 className="h4">{this.props.section.name}</h3>
                        <a id={"mod-common/modules/opl-expander-2-control-" + this.props.idx} role="tab"
                           className="opl-section__expander-trigger" href="#"
                           aria-controls={"expander-content-" + this.props.idx} aria-expanded="true">
                            <span className="u-acc-hide">rozwiń sekcję</span>
                            <span className="u-acc-hide">zwiń sekcję</span>
                        </a>
                    </div>
                    <div className="opl-section__content" id={"expander-content-" + this.props.idx} role="tabpanel"
                         aria-labelledby={"mod-common/modules/opl-expander-2-control-" + this.props.idx}
                         aria-hidden="false">
                        <div className="opl-section__content-inner u-no-padding">
                            <table className="opl-table opl-table-compare">
                                <caption className="u-acc-hide">{this.props.section.name}</caption>
                                <tbody>
                                {this.props.section.attributesRowDTOList && this.props.section.attributesRowDTOList.map((row, index) =>
                                    <ComparatorRowComponent row={row} idx={index} markDiff={this.props.markDiff}/>
                                )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                :
                null
        )
    }

}
